<style lang="less" scoped>
  @import url('../assets/css/vipPage.less');
</style>

<template>
        <div class="flex_middle"  @click="course_list(item)">
        <login v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
          <div class="course_header_img"><img :src="item.coverImage ? item.coverImage : defaultCoverImg" alt="VIP课程"></div>
          <div class="title">{{item.title}}</div>
          <div class="course_text_detail">
            <div>
              <p class="teacher" v-if="item.teacherNicknames">讲师:{{item.teacherNicknames[0]}}</p>
              <p class="teacher" v-if="!item.teacherNicknames">讲师:佚名</p>
              <p class="course_text_detail_pad"><img src="../assets/img/people1.png" alt="人数" class="course_text_detail_pad_img">{{item.totalStudent | numFm}}</p>
            </div>
            <div class="attention">
              <img  v-if="item.isFollowed == 'N'" :src="collection" @click.stop="favorite(item)">
              <img  v-if="item.isFollowed == 'Y'" :src="flowed" @click.stop="favorite(item)">
            </div>
          </div>
        </div>
</template>
<script>
import login from '@/components/login'
export default {
    components: {
        login
    },
    props: [
        'item'
    ],
    computed: {
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }
    },
    data () {
        return {
            // 未收藏ICON
            collection: require('../assets/img/heart_g.png'),
            // 已收藏ICON
            flowed:  require('../assets/img/heart_r.png'),
            // 是否显示登录框
            showLoginModal: 0,
            defaultCoverImg: require('../assets/img/course-large.png')
        }
    },
    methods: {
        showDialogEvent(data) {
            this.showLoginModal = data
        },
        // 进入VIP课程详细信息页面
        course_list(info) {
        if (info.courseType == 'VIP') {
          this.$router.push({
            name: 'vipDetail',
            params: {
              id: info.courseId
            }
          })
        } else if (info.courseType == 'VOD') {
          if(info.lastCurriculum){
            this.$router.push({
              name: 'live',
              params: {
                id: info.lastCurriculum.liveChannelId,
                courseId: info.courseId
              }
            })
          }else{
            this.$Toast({
              message: '暂无直播',
              position: 'middle',
              duration: 1000
            })
          }
        }
      },
      // 收藏课程或移除已收藏课程
      favorite(item) {
        if (!this.LOGIN_USER_OBJECT) {
          this.showLoginModal = 1
          return
        }
        if (item.isFollowed == 'N') {
          this.addFavoriteCourse(item)
        } else {
          this.deleteFavoriteCourse(item)
        }
      },
      addFavoriteCourse(item){
        this.axios.post('/api/favorite', {
            courseId: item.courseId
          }).then(data => {
            if (data.data == '添加成功') {
              this.$Toast({
                message: data.data,
                position: 'middle',
                duration: 1000
              })
              this.$set(item, 'isFollowed', 'Y')
            }
          }).catch(err => {
            console.log(err)
          });
      },
      deleteFavoriteCourse(item){
        this.axios.post('/api/favorite/delete', {
            courseId: item.courseId
          }).then(data => {
            if (data.data == '移除成功') {
              this.$Toast({
                message: data.data,
                position: 'middle',
                duration: 1000
              })
              this.$set(item, 'isFollowed', 'N')
            }
          }).catch(err => {
            console.log(err);
          });
      },
    }
}
</script>



