<template>
  <div class="course_list">
    <p class="noWrapper" v-if="!live.length"><img src="../../assets/img/da.png" alt="没有关注的课程哦~"><br>暂时还没有内容~~宝宝先看看别的~~</p>
    <section>
      <div v-for="(item, index) in live" class="live_item" @click.stop="course_list(item)" v-if="item.isRecommended == 'Y'" :key="index">
        <div class="content_bottom content_bottom_one">
          <div class="middel_img_header">
            <div class="middel_img_header_text">
            </div>
            <div class="middel_img_header_right_img">
              <img :src="item.recommendImage" alt="课程">
            </div>
          </div>
          <div class="middle_img_footer">
            <div class="middle_detail">
              <div>
                <span><span>{{item.title}}</span> </span>
                <span><img src="../../../static/images/people1.png" class="course_text_detail_pad_img course_text_detail_pad_img_big">{{item.totalStudent | numFm}}</span>
              </div>
            </div>
            <div class="middle_attention">
              <img @click.stop="favorite(item)" :src="item.isFollowed == 'Y' ? flowed : collection">
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="course_flex">

      <course-category v-for="(item, index) in live" :item="item" :key="index" v-if="item.isRecommended == 'N'"></course-category>
      
    </section>
    
  </div>
</template>
<script>
  import courseCategory from '@/components/courseCategory'
  export default {
    name: 'freePage',
    methods: {},
    data() {
      return {
        courses: [],
        collection: require('../../assets/img/heart_g.png'),
        flowed: require('../../assets/img/heart_r.png'),
        item: {
          courseId: null
        },
        live: [],
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
        defaultCoverImg: require('../../assets/img/course-large.png')
      }
    },
    components: {
      courseCategory
    },
    created () {
      window.onscroll = ()=>{
        this.dropDown()
      }
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.get('/api/displaywindow/favorite', {
        params: {
          page: this.page
        }
      }).then(res => {
        this.live = res.data
        this.$Indicator.close()
      }).catch(err => {
        console.log(err)
      });
    },
    methods: {
      favorite(item) {
        if (!this.$store.state.userModule.LOGIN_USER_OBJECT) {
          this.$Toast({
            message: '请登录后操作',
            position: 'middle',
            duration: 1000
          })
          return
        }
        if (item.isFollowed == 'N') {
          this.axios.post('/api/favorite', {
            courseId: item.courseId
          }).then(data => {
            if (data.data == '添加成功') {
              this.$set(item, 'isFollowed', 'Y')
              this.$Toast({
                message: '添加成功',
                position: 'middle',
                duration: 500
              })
            }
          }).catch(err => {
            console.log(err)
          })
        } else {
          this.axios.post('/api/favorite/delete', {
            courseId: item.courseId
          }).then(data => {
            console.log(data)
            if (data.data == '移除成功') {
              this.$set(item, 'isFollowed', 'N')
              this.$Toast({
                message: '移除成功',
                position: 'middle',
                duration: 500
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return;
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1
          this.axios.get('/api/displaywindow/favorite', {
            params: {
              page: this.page
            }
          }).then(res => {
            if (this.pagination) {
              if (res.data && res.data.length > 0) {
                this.live = this.live.concat(res.data)
                this.$Indicator.close()
              } else {
                this.$Indicator.close()
                this.pagination = false
                this.showData = true
              }
            }
          }).catch(err => {
            console.log(err)
          });
        }
      },
      course_list(info) {
        if (info.courseType == 'VIP') {
          this.$router.push({
            name: 'vipDetail',
            params: {
              id: info.courseId
            }
          })
        } else if (info.courseType == 'VOD') {
          if(info.lastCurriculum){
            this.$router.push({
              name: 'live',
              params: {
                id: info.lastCurriculum.liveChannelId,
                courseId: info.courseId
              }
            })
          }else{
            this.$Toast({
              message: '暂无直播',
              position: 'middle',
              duration: 1000
            })
          }
        }
      }
    }
  }
</script>
<style lang="less" scoped>
@import url('../../assets/css/studyPage.less');
  .noWrapper{
    text-align: center;
    &>img{
      width: 400px;
      margin: 40px 0 20px 0;
    }
    color: #aaa;
    font-size: 30px;
  }
</style>



